@import "../style/variable.scss";

footer{
    background-color: $bgcolor2;
    .footer_logo{
        width: 18rem;
        height: 8rem;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    a{
        transition: all .3s ease;
        text-decoration: none;
        color: $white;;
        P{
            padding-left: .5rem;
        }
        &:hover{
            color: $bgShadow;
        }
    }
}