@import "../fonts/fonts.scss";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    font-size: 1.2vw;
    @media screen and (max-width:650px) {
        font-size: 2vw;
    }
}

$bgcolor: #ffe6e1;
$bgcolor2: #fad1c9;
$bgShadow: #6262625e;
$bgShadow2: #4040404b;
$bgShadow3: #ffe6e1;
$color: #584f4d;
$white: #fff;

$bgGradinet1: linear-gradient(90deg, rgba(255, 230, 225, 1) 0%, rgb(221, 187, 180) 100%);

@mixin button {
    background: $bgcolor;
    border: none;
    padding: 1rem 2rem 1rem 2rem;
    color: $color;
    transition: all 0.3s ease;
    border: 3px solid $bgcolor;
    border-radius: .3rem;
    &:hover {
        background: $bgShadow;
        color: $bgcolor;
    }
}

@mixin  activetn {
   background-color:transparent !important;
   color: $color !important;
}
h2{
    color: $white;
    font-family: gothampro_black;
}
.swiper-pagination{
    display: none;
}