@import "../style/variable.scss";

.video_and_comment {
    position: relative;
    height: 45rem;
    overflow: hidden;

    .video_and_commentBG {
        background-image: url("../assets/images/background2.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        filter: blur(8px);
        -webkit-filter: blur(8px);
        width: 100%;
        height: 100%;
    }
    .ideo_and_commentContent {
        font-weight: bold;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 2;
        padding: 20px;
        text-align: center;
        .wrapper {
            display: flex;
            flex-direction: column;
            .wraper_item {
                display: flex;
                justify-content: space-between;
                width: 100%;
                margin: 0 0 2rem 0;
                .images_wraper {
                    width: 47%;
                    height: 15rem;
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
            }
        }
        .videoBlock{
            width: 47%;
            height: 27rem;
            video{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}
