@import "../style/variable.scss";

.navbar {
    background: $bgGradinet1;
    font-family: gothampro_medium;
    .navbar-brand {
        .logo {
            width: 10rem;
            height: 4rem;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    .nav-link{
        color: $color !important;
        transition: all .3s ease;
        cursor: pointer;
        &:hover{
        color: $white !important;
        }
    }
    
}
