@import "../style/variable.scss";

.slider {
  position: relative;
  height: 100%;
  background: $bgcolor;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
  padding: 2rem 0;
  .swiper {
    width: 100%;
    .swiper-slide {
      background-position: center;
      background-size: cover;
      width: 300px;
      height: 300px;
      img {
        display: block;
        width: 100%;
        height: 80%;
        object-fit: contain;
      }
    }
  }
}
