
@import "../style/variable.scss";
.curss{
    justify-content: space-around;
    width: 100%;
    height: 100vh;
    margin-top: 4rem;
    .curs-img{
        width: 15rem;
        height: 10rem;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover
        }
    }
    .buttons{
        .button{
            @include button;
            padding: 1rem;
            margin-right: .5rem;
        }
    }
    .modall{
        width: 100%;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        background-color: rgba(147, 147, 147, 0.561);
        display: none;
        .modal_content{
            display: flex;
            flex-direction: column;
            .cloaseModal{
                display: flex;
                justify-content: space-between;
            }
            .content{
                display: flex;
                justify-content: center;
                align-items: center;
                form{
                    width: 20rem;
                    height: 6rem;
                    background-color: aliceblue;
                    .edit{
                        @include button;
                        padding:.5rem .5rem .5rem .5rem;
                        margin: 0 auto;
                    }
                    .close-btn{
                        cursor: pointer;
                    }
                }
            }
        }
    }
}

ol{
    margin-left: 0; 
    padding-left: 0;
    li{
        list-style-type:none
    }
    .item-date{
        cursor: pointer;
        div{
            margin-left: 5px;
       }        
    }     

}