@import "../style/variable.scss";

.content_package{
background-color: $bgcolor2;
min-height: 100vh;
    .cont{
        margin-top: 2rem;
        .curs_Connect{
            display: flex;
            flex-direction: column;
            align-items: center;
            
            .cursImages{
                width: 60%;
                height: 20rem;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
            .curs_description{
                margin-top: 1rem;
                color: $white;
            }
        }
        label{
            color: $white
        }
        button{
            @include button;
            margin: 0 auto;
        }
        .free{
            display: none;
        }
    }
}
.time_and_day{
    color: #ffffff;
    font-size: 18px;

}