@import "../style/variable.scss";


.teacher_seconds{
    position: relative;
    .imgGimastcs{
        position: absolute;
        width: 100%;
        height: 100%;
      img{
        width: 100%;
        height: 100%;
        object-fit: revert;
      }
    }
    .shadowBg{
        background-color: $bgShadow2;
        .info{
            width: 40%;
            height: 100%;
            color: $white;
            button{
                @include button
            }
            @media screen and (max-width:650px) {
                width: 60%; 
            }
        }
    }
}